export const getRandomQuote = arrayOfQuotes => {
  let max = arrayOfQuotes.length,
    min = 0;
  max = Math.floor(max);
  let index = Math.floor(Math.random() * (max - min)) + min;
  while (arrayOfQuotes[index].length > 67) {
    index = Math.floor(Math.random() * (max - min)) + min;
  }
  return arrayOfQuotes[index];
};
