import React from "react";
import { Switch, Route, BrowserRouter as Router } from "react-router-dom";
import Thumbnail from "../Thumbnail/Thumbnail";
import HeroPage from "../HeroPage/HeroPage";
import styles from "./Main.module.css";
import "../../App.css";
import heroesArray from "../../data";
import { getRandomQuote } from "../util";

const Component = () => (
  <div className={`${styles["body"]}`}>
    {heroesArray.map((hero, i) => {
      let randomQuote = getRandomQuote(hero.quotes);
      let path = hero.name
        .toLowerCase()
        .split(" ")
        .join("_");

      return (
        <div className="thumbnail">
          <Thumbnail
            key={i}
            hero={hero}
            randomQuote={randomQuote}
            path={path}
          />
        </div>
      );
    })}
  </div>
);

function Main() {
  return (
    <Router>
      <div className="app">
        <div className={`${styles["header"]}`}>
          <span className={`${styles["header__logo-text"]}`}>
            I N S P I R E D
          </span>
        </div>

        <Route exact path="/" component={Component} />
        {heroesArray.map((hero, i) => {
          let path = hero.name
            .toLowerCase()
            .split(" ")
            .join("_");
          console.log(i)
          return (
            <Route path={"/" + path} render={props => <HeroPage {...props} hero={hero} />} />
          )
        })}


      </div>
    </Router>
  );
}

Main.propTypes = {};

export default Main;
