import React from "react";
import styles from "./HeroPage.module.css";

function HeroPage({ hero }) {
  let { name, photo, quotes, bio } = hero;
  return (
    <div className={`${styles["hero"]}`}>
      <div className={`${styles["hero__name"]}`} />
      <div className={`${styles["image-container"]} ${styles["container__width_960"]}`}>
        <img src={photo} alt={name} />
      </div>
      <div className={`${styles["text"]}`}>
        <div className={`${styles["text__container"]} ${styles["container__width_960"]}`}>
          <div className={`${styles["hero__bio"]}`}>{bio}</div>
          <div className={`${styles["quotes"]}`}>
            {quotes.map(quote => (
              <div className={`${styles["quotes_quote"]}`}>
                <q className={`${styles["q"]}`}>{quote}</q>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default HeroPage;
