const bio = `Vincent Thomas Lombardi was an American football player, coach, and executive in the National Football League (NFL). He is best known as the head coach of the Green Bay Packers during the 1960s, where he led the team to three straight and five total NFL Championships in seven years, in addition to winning the first two Super Bowls at the conclusion of the 1966 and 1967 NFL seasons. Following his sudden death from cancer in 1970, the NFL Super Bowl trophy was named in his honor. He was enshrined in the Pro Football Hall of Fame in 1971, the year after his death. Lombardi is considered by many to be the greatest coach in football history, and he is more significantly recognized as one of the greatest coaches and leaders in the history of any American sport.`;

