import React, { useState } from "react";
import { Link } from "react-router-dom";
import styles from "./Thumbnail.module.css";
import ThumbnailText from "../Thumbnail/ThumnnailText";
import "../../App.css";

function Thumbnail({ hero, randomQuote, path }) {
  const [hover, setHover] = useState(false);

  let onMouseEnter = () => {
    setHover(true);
  };

  let onMouseLeave = () => {
    setHover(false);
  };

  let cssHoverOn = `${styles["thumbnail__border-radius_round"]} ${
    styles["image__hover_on"]
    } ${styles["cursor_pointer"]}`;

  let cssHoverOff = `${styles["thumbnail__border-radius_round"]}`;

  return (
    <Link to={path}>
      <div
        className={
          hover
            ? `${styles["thumbnail__border-radius_round"]} ${
            styles["thumbnail"]
            } ${styles["cursor_pointer"]}`
            : `${styles["thumbnail__border-radius_round"]} ${
            styles["thumbnail"]
            }`
        }
        onMouseOver={onMouseEnter}
        onMouseOut={onMouseLeave}
      >
        <div className={`${styles["image"]}`}>
          <img
            className={hover ? cssHoverOn : cssHoverOff}
            src={hero.photo}
            alt={hero.name}
          />
          <span
            className={
              hover
                ? `${styles["image__text__on"]}`
                : `${styles["image__text__off"]}`
            }
          >
            {hero.name}
          </span>
        </div>
        <ThumbnailText
          randomQuote={randomQuote}
          onMouseEnter={onMouseEnter}
          onMouseLeave={onMouseLeave}
        />
      </div>
    </Link>
  );
}

export default Thumbnail;
